<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" class="input-with-select">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderlist" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="order_number" label="" width="600px">
        </el-table-column>
        <el-table-column prop="order_price" label="" width="80px">
        </el-table-column>
        <el-table-column prop="pay_status" label="" width="80px">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.pay_status==='0'"></el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="80px">
        </el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scope">
            {{scope.row.create_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showeditAddressDialog(scope.row)">
            </el-button>
            <el-button type="success" icon="el-icon-location" size="mini" @click="showProgress"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum" :page-sizes="[1, 2, 5, 10]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next,
         jumper" :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editAddressDialogVisible" width="50%" @close="editAddressDialogClosed">
      <el-form :model="editAddressForm" :rules="editAddressFormRules" ref="editAddressFormRef" label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader v-model="editAddressForm.address1" :props="{ expandTrigger: 'hover' }" :options="cityData"
            @change="handleChange" clearable>
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="editAddressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editAddressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editAddressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible" width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
  import cityData from './citydata.js'
  export default {
    data() {
      return {
        // 
        orderlist: [],
        // 
        queryInfo: {
          // 
          query: '',
          // 
          pagenum: 1,
          // 
          pagesize: 10
        },
        total: 0,
        // 
        editAddressDialogVisible: false,
        // 
        editAddressForm: {
          address1: [],
          address2: ''
        },
        // 
        editAddressFormRules: {
          address1: [{
            requried: true,
            message: '',
            trigger: 'blur'
          }],
          address2: [{
            requried: true,
            message: '',
            trigger: 'blur'
          }]
        },
        cityData,
        // 
        progressVisible: false,
        // 
        progressInfo: []
      }
    },
    created() {
      this.getOrderList()
    },
    methods: {
      async getOrderList() {
        const {
          data: res
        } = await this.$http.get('orders', {
          params: this.queryInfo
        })
        console.log(res.data);
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // this.$message.success('')
        this.orderlist = res.data.goods
        this.total = res.data.total
      },
      // pagesize
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize
        // pagesize
        this.getOrderList()
      },
      // pagenum
      handleCurrentChange(newPage) {
        // pagenum
        this.queryInfo.pagenum = newPage
        this.getOrderList()
      },
      // 
      showeditAddressDialog(info) {
        this.editAddressDialogVisible = true
        console.log(info);
      },
      // 
      editAddressDialogClosed() {
        // 
        this.$refs.editAddressFormRef.resetFields()
      },
      // 
      handleChange() {
        console.log(this.editAddressForm);
      },
      // 
      async showProgress() {
        const {
          data: res
        } = await this.$http.get('/kuaidi/1106975712662')
        // console.log(res.data);
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.progressInfo = res.data
        this.progressVisible = true
        console.log(this.progressInfo);
      }
    },
  }

</script>

<style lang="less" scoped>
  @import '../../plugins/timeline/timeline.css';
  @import '../../plugins/timeline-item/timeline-item.css';

  .el-cascader {
    width: 100%;
  }

</style>
